	setCustomEditMenuIcon( id, context_btn ) {
		switch ( id ) {
			case 'login':
				this.setEditMenuLoginIcon( context_btn );
				break;
			case 'import_icon':
			case 'client_contact':
			case 'invoice':
			case 'transaction':
			case 'payment_method':
				this.setNavigationMenuIcons( context_btn );
				break;
		}
	}

	setNavigationMenuIcons( context_btn ) {
		if ( this.edit_only_mode ) {
			ContextMenuManager.hideMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, false )
		}
	}

	setEditMenuLoginIcon( context_btn ) {
		if ( !PermissionManager.validate( 'company', 'login_other_user' ) ) {
			ContextMenuManager.hideMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, false )
			return;
		}

		ContextMenuManager.disableMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, false );
	}

	initSubClientContactView() {
		var $this = this;

		if ( !this.current_edit_record.id ) {
			TTPromise.resolve( 'BaseViewController', 'onTabShow' ); //Since search() isn't called in this case, and we just display the "Please Save This Record ..." message, resolve the promise.
			return;
		}

		if ( this.sub_client_contact_view_controller ) {
			this.sub_client_contact_view_controller.buildContextMenu( true );
			this.sub_client_contact_view_controller.setDefaultMenu();
//			$this.sub_client_contact_view_controller.parent_key = 'client_id';
			$this.sub_client_contact_view_controller.parent_value = $this.current_edit_record.id;
			$this.sub_client_contact_view_controller.parent_edit_record = $this.current_edit_record;
			$this.sub_client_contact_view_controller.initData();
			return;
		}

		Global.loadScript( 'views/invoice/client_contact/ClientContactViewController.js', function() {
			var tab_client_contacts = $this.edit_view_tab.find( '#tab_client_contacts' );
			var firstColumn = tab_client_contacts.find( '.first-column-sub-view' );

			Global.trackView( 'Sub' + 'ClientContact' + 'View' );
			ClientContactViewController.loadSubView( firstColumn, beforeLoadView, afterLoadView );

		} );

		function beforeLoadView() {

		}

		function afterLoadView( subViewController ) {
			$this.sub_client_contact_view_controller = subViewController;
			$this.sub_client_contact_view_controller.parent_key = 'client_id';
			$this.sub_client_contact_view_controller.parent_value = $this.current_edit_record.id;
			$this.sub_client_contact_view_controller.parent_edit_record = $this.current_edit_record;
			$this.sub_client_contact_view_controller.parent_view_controller = $this;
			$this.sub_client_contact_view_controller.postInit = function() {
				this.initData();
			};
		}
	}

